.sideBar {
  width: 296px;
  position: fixed;
  top: 0;
  bottom: 0;
  background: $primary-color;
  font-weight: bold;

  a {
    text-decoration: none;
    display: block;
  }

  i {
    margin-right: 16px;
  }

  &-list {}

  &-list-item {
    margin-bottom: 24px;

    a {
      margin: 0 -32px;
      padding-left: 32px;

      &:hover,
      &:focus {
        border-left: 3px solid $secondary-color;
        color: $secondary-color;
      }
    }
  }

  .account {
    font-size: 14px;
    color: #0000009A;
    margin-top: auto;

    h5 {
      color: #000;
      font-size: 16px;
      font-weight: bold;
      margin-bottom: 0.25rem;
    }
  }
}