a {
  color: #000;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  line-height: 1.5;
  font-family: 'Lato', sans-serif;
}

i {
  vertical-align: bottom;
}

p {
  margin-bottom: 0;
}

h2 {
  font-size: 24px;
  font-weight: bold;
}

main {
  margin-left: 296px;
}

.container {
  padding: 32px;
  height: 100vh;
}

.list-group-item.active {
  background-color: $primary-color;
  border-color: #CDDDCD;
  color: #000;
}

.accountPhoto {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.pri-bg {
  background-color: $primary-color;
  border-color: #CDDDCD;
  color: #000;
}

.sec-bg {
  background-color: $secondary-color;
  color: #fff;
}

.pri-btn {
  background-color: $secondary-color;
  color: #fff;

  &:hover {
    color: #fff;
  }
}

.sec-btn {
  color: $secondary-color;
  border-color: $secondary-color;

  &:hover {
    background-color: $secondary-color;
    color: #ffffff;
    border-color: $secondary-color;
  }
}

.btn:focus {
  box-shadow: none;
}

.secondary-color {
  color: $secondary-color;
}

.sec-text {
  font-size: 12px;
  color: #707070;
}

.cancel-btn {
  color: $secondary-color;
  border-color: $secondary-color;
}

.rotate90 {
  transform: rotate(-90deg);
}